@font-face {
  /*Libre Baskerville*/
  font-family: 'Libre Baskerville';
  src: url_css('fonts/LebreBaskervile/librebaskerville-bold/librebaskerville-bold.eot');
  src: url_css('fonts/LebreBaskervile/librebaskerville-bold/librebaskerville-bold.eot#iefix') format('embedded-opentype'),
  url_css('fonts/LebreBaskervile/librebaskerville-bold/librebaskerville-bold.woff2') format('woff2'),
  url_css('fonts/LebreBaskervile/librebaskerville-bold/librebaskerville-bold.svg') format('svg'),
  url_css('fonts/LebreBaskervile/librebaskerville-bold/librebaskerville-bold-light.ttf') format('truetype'),
  url_css('fonts/LebreBaskervile/librebaskerville-bold/librebaskerville-bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

